import Link from "next/link";
import React from "react";

const LoginPage = () => {
  useRoute
  return (
    <div className="min-h-screen flex items-center justify-center w-full">
      <div className="w-full bg-white shadow-sm rounded-lg max-w-md min-h-[300px] p-8">
        <h1 className="text-center text-2xl mb-6 font-bold">Login</h1>
        <div className="space-y-6">
          {/* Input Fields */}
          <div className="flex flex-col gap-5">
            <div>
              <label className="block mb-1 text-[#344767]">Email:</label>
              <input
                type="text"
                placeholder="Enter your email"
                className="w-full border border-[#E1E8F5] text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4] rounded-lg px-4 py-2"
              />
            </div>
            <div>
              <label className="block mb-1 text-[#344767]">Password:</label>
              <input
                type="password"
                placeholder="Enter your password"
                className="w-full border border-[#E1E8F5] text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4] rounded-lg px-4 py-2"
              />
            </div>
          </div>
          {/* forgot password */}
          <div className="text-right">
            <Link
              href="/login"
              className="text-sm text-[#2E69A4] hover:underline"
            >
              Forgot Password?
            </Link>
          </div>
          {/* Actions */}
          <div className="flex flex-col gap-2">
            <button className="w-full focus:outline-none py-2 px-y cursor-pointer text-lg rounded-lg shadow-sm hover:bg-[#162038] text-white bg-[#1b2a49]">
              Login
            </button>
            <button className="w-full focus:outline-none py-2 px-y cursor-pointer text-lg rounded-lg  text-[#344767] hover:bg-[#E1E8F5] bg-white border border-[#2E69A4]">
              Create Account
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
